<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="row text-gray-dark d-flex flex-row-reverse">
      <div class="col-lg-4 mb-4">
        <div class="card cart-info-card">
          <div class="card-header">訂單資訊</div>
          <div class="card-body">
            <p class="card-text">
              <span>小計：</span>
              <span>{{carts.total | currency}}</span>
            </p>
            <p class="card-text">
              <span>運費：</span>
              <span>—</span>
            </p>
            <p class="card-text">
              <span>折扣：</span>
              <span>{{ (carts.coupon == undefined) ? "—" : carts.coupon.percent + "%" }}</span>
            </p>
            <p class="card-text">
              <span>總計：</span>
              <span>{{ carts.final_total | currency }}</span>
            </p>
            <div v-if="carts.coupon == undefined">
              <div class="form-group">
                <input class="form-control" type="text" placeholder="輸入優惠券" v-model="coupon_code" />
              </div>
              <button class="btn btn-cyan btn-block" @click="addCouponCode">加入優惠券</button>
            </div>
          </div>
        </div>
      </div>
      <div class="col-lg-8">
        <p class="h4 pb-2 mb-3" style="border-bottom: 1px solid rgba(0, 0, 0, 0.1)">
          <i class="fas fa-address-card"></i> 收件人資訊
          <small class="ml-2 text-gray remind">
            <span class="text-danger">*</span> 為必填項目
          </small>
        </p>
        <form @submit.prevent="createOrder">
          <div class="form-row">
            <div class="form-group col-md-6">
              <label for="useremail">
                <span class="text-danger">*</span>Email
              </label>
              <input
                type="email"
                class="form-control"
                id="useremail"
                placeholder="Email"
                name="email"
                v-model="form.user.email"
                v-validate="'required|email'"
                :class="{'is-invalid' : errors.has('email')}"
              />
              <span
                class="text-danger"
                v-if="errors.has('email')"
              >{{ errors.first('email').replace(/email/,'Email') }}</span>
            </div>
            <div class="form-group col-md-6">
              <label for="username">
                <span class="text-danger">*</span>收件人姓名
              </label>
              <input
                type="text"
                class="form-control"
                id="username"
                name="name"
                v-model="form.user.name"
                v-validate="'required'"
                :class="{'is-invalid' : errors.has('name')}"
                placeholder="輸入收件人姓名"
              />
              <span
                class="text-danger"
                v-if="errors.has('name')"
              >{{ errors.first('name').replace(/name/,'收件人姓名') }}</span>
            </div>
          </div>
          <div class="form-group">
            <label for="usertel">
              <span class="text-danger">*</span>收件人電話
            </label>
            <input
              type="tel"
              class="form-control"
              name="tel"
              id="usertel"
              v-model="form.user.tel"
              v-validate="'required|digits:10'"
              :class="{'is-invalid' : errors.has('tel')}"
              placeholder="輸入收件人電話"
            />
            <span
              class="text-danger"
              v-if="errors.has('tel')"
            >{{ errors.first('tel').replace(/tel/,'收件人電話') }}</span>
          </div>
          <div class="form-group">
            <label for="inputAddress2">
              <span class="text-danger">*</span>收件人地址
            </label>
            <input
              type="text"
              class="form-control"
              name="address"
              id="useraddress"
              v-model="form.user.address"
              v-validate="'required'"
              :class="{'is-invalid' : errors.has('address')}"
              placeholder="輸入收件人地址"
            />
            <span
              class="text-danger"
              v-if="errors.has('address')"
            >{{ errors.first('address').replace(/address/,'收件人地址') }}</span>
          </div>
          <div class="form-group">
            <label for="comment">留言</label>
            <textarea
              name
              id="comment"
              class="form-control"
              cols="30"
              rows="10"
              v-model="form.message"
            ></textarea>
          </div>
          <div class="form-group d-flex justify-content-between mb-0">
            <router-link to="/checkout/shippingCart" class="btn btn-outline-dark">
              <i class="fas fa-angle-left"></i> 返回購物車
            </router-link>
            <button type="submit" class="btn btn-cyan">
              訂單確認
              <i class="fas fa-angle-right"></i>
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      carts: [],
      coupon_code: "",
      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: ""
        },
        message: ""
      },
      isLoading: false
    };
  },
  methods: {
    getCart() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        if (response.data.data.total != 0) {
          vm.carts = response.data.data;
        }
        vm.isLoading = false;
      });
    },
    addCouponCode() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
      const couponData = {
        code: vm.coupon_code
      };
      vm.isLoading = true;
      this.$http.post(api, { data: couponData }).then(response => {
        if (response.data.success) {
          this.$bus.$emit("showMessage", response.data.message, "success");
          vm.getCart();
          this.$bus.$emit("cartReload");
        } else {
          vm.isLoading = false;
          this.$bus.$emit("showMessage", response.data.message, "danger");
        }
      });
    },
    createOrder() {
      this.$validator.validateAll().then(result => {
        if (result) {
          const vm = this;
          const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
          vm.isLoading = true;
          this.$http.post(api, { data: vm.form }).then(response => {
            vm.isLoading = false;
            if (response.data.success) {
              console.log(response.data.orderId);
              vm.$router.push(`/checkout/review_payment/${response.data.orderId}`);
            } else {
              this.$bus.$emit("showMessage", response.data.message, "danger");
            }
          });
        } else {
          this.$bus.$emit("showMessage", "請完整輸入收件人資訊", "danger");
        }
      });
    }
  },
  created() {
    this.$emit("breadData", "訂單資訊");
    if (Object.getOwnPropertyNames(this.$route.params).length > 0) {
      this.carts = this.$route.params.carts;
    } else {
      this.getCart();
    }
  }
};
</script>

<style scoped>
.cart-info-card .card-text {
  display: flex !important;
  -webkit-box-pack: justify !important;
  -ms-flex-pack: justify !important;
  justify-content: space-between !important;
}
.remind {
  font-size: 60%;
  color: #6c757d !important;
}
</style>



