<template>
  <footer class="bg-gray-dark text-white pt-5 pb-0">
    <div class="container">
      <div class="row d-flex justify-content-center">
        <div class="col-lg-3 col-md-6 col-12">
          <h5 class="mb-3">CATEGORY</h5>
          <ul class="list-unstyled small">
            <li v-for="item in category" :key="item">
              <router-link :to="{name: 'Products', params: {'categoryFromHeader': item}}">{{item}}</router-link>
            </li>
          </ul>
        </div>
        <div class="col-lg-3 col-md-6 col-12">
          <h5 class="mb-3">關於網站</h5>
          <p class="small">
            使用
            <a href="https://cli.vuejs.org/" target="_brank">Vue CLI</a>、
            <a href="https://getbootstrap.com/" target="_brank">Bootstrap 4</a> 開發。
          </p>
          <p class="small">為個人練習使用無任何商業用途。</p>
          <p class="small">
            圖片來源：
            <a href="https://unsplash.com/" target="_brank">Unsplash</a>
          </p>
        </div>
      </div>
      <div class="row copyright">
        <div class="col-12">
          <p class="text-center py-1 m-0 small">Copyright @ 2019 | Website design by Jerry</p>
        </div>
      </div>
    </div>
  </footer>
</template>

<script>
export default {
  props: ["category"]
};
</script>

<style scoped>
footer a {
  padding: 0rem 0.5rem;
  color: white;
}
footer a:hover {
  color: #d9d9d9;
}
.list-unstyled li {
  margin-bottom: 1rem;
}
.copyright {
  border-top: 1px solid #6c757d;
}
</style>



