<template>
  <div class="dropdown-menu dropdown-menu-lg-right p-3" id="car-dropdown">
    <loading :active.sync="isLoading"></loading>
    <template v-if="carts.total == 0">
      <div class="text-center p-5">
        <p class="h2">
          <i class="fas fa-cart-arrow-down"></i>
        </p>
        <p class="h5">你的購物車是空的</p>
        <p class="mt-4">
          <router-link
            :to="{name: 'Products', params: {'categoryFromHeader': 'all'}}"
            class="btn btn-cyan"
          >前往購物</router-link>
        </p>
      </div>
    </template>
    <template v-else>
      <form>
        <div>
          <div class="cart-list px-3 py-2 mb-1" v-for="item in carts.carts" :key="item.id">
            <span class="cart-remove text-pink" @click="removeCartItem(item.id)">
              <i class="fas fa-times"></i>
            </span>
            <div class="cart-list-info">
              <p class="h6 mb-1">
                <router-link
                  :to="{name: 'ProductDetail', params: {'id' : item.product.id}}"
                >{{ item.product.title }}</router-link>
              </p>
              <p class="mb-0 text-secondary">
                {{ item.qty }} x {{ item.product.price | currency }}
                <span
                  class="small ml-1 text-success"
                  v-if="typeof item.coupon !== 'undefined'"
                >已套用優惠券</span>
              </p>
            </div>
          </div>
        </div>
        <hr class="mt-0 mb-2" />
        <div class="cart-final-total d-flex justify-content-between px-2 mb-4">
          <span>Total:</span>
          <span v-if="carts.final_total !== carts.total">
            <del class="text-gray">{{ carts.total | currency }}</del>
            {{ carts.final_total | currency }}
          </span>
          <span v-else>{{ carts.total | currency }}</span>
        </div>
        <div class="cart-btn-area d-flex justify-content-between">
          <router-link to="/checkout/shippingCart" class="btn btn-sm btn-outline-dark">瀏覽購物車</router-link>
          <router-link to="/checkout/OrderInfo" class="btn btn-sm btn-cyan">結帳</router-link>
        </div>
      </form>
    </template>
  </div>
</template>

<script>
export default {
  data() {
    return {
      carts: [],
      isLoading: false
    };
  },
  methods: {
    getCart() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      let totalQty = 0;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        vm.carts = response.data.data;
        response.data.data.carts.forEach(element => {
          totalQty += element.qty;
        });
        vm.totalQty = totalQty;
        vm.$emit("cartQty", totalQty);
        vm.isLoading = false;
      });
    },
    removeCartItem(id) {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(api).then(response => {
        this.getCart();
        vm.isLoading = false;
        if (response.data.success) {
          this.$bus.$emit("showMessage", "商品已從購物車移除", "success");
        } else {
          this.$bus.$emit("showMessage", response.data.message, "danger");
        }
      });
    }
  },
  created() {
    this.getCart();
    const vm = this;
    vm.$bus.$on("cartReload", () => {
      vm.getCart();
    });
  }
};
</script>

<style scoped>
@media (min-width: 992px) {
  .dropdown-menu {
    width: 300px;
  }
}

.cart-list {
  position: relative;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
div.cart-list:last-child {
  border: none;
  margin-bottom: 0 !important;
}

.cart-remove {
  position: absolute;
  right: 10px;
  top: 19px;
  cursor: pointer;
}
</style>



