<template>
  <nav aria-label="breadcrumb">
    <ol class="breadcrumb mb-0 p-0">
      <li class="breadcrumb-item">
        <router-link to="/"><i class="fas fa-home"></i> Home</router-link>
      </li>
      <li class="breadcrumb-item">
        <router-link to="/products/all" v-if="clickCategory != 'all' || page === 'search'">所有商品</router-link>
        <span v-else>所有商品</span>
      </li>
      <li class="breadcrumb-item" v-if="clickCategory != 'all'">
        <span v-if="page == 'products'">{{clickCategory}}</span>
        <router-link :to="categoryLink" v-else>{{clickCategory}}</router-link>
      </li>
    </ol>
  </nav>
</template>

<script>
export default {
  props: ["clickCategory", "page", "keyword"],
  data() {
    return {
      categoryLink: "",
    };
  },
  watch: {
    clickCategory(newVal, oldVal) {
      if (this.page != "products") {
        const vm = this;
        vm.categoryLink = `/products/${newVal}`;
      }
    }
  }
};
</script>

<style scoped>
.breadcrumb {
  background-color: white !important;
}
.keyword {
  font-size: 1rem;
  line-height: 31px
}
</style>



