import Vue from "vue";
import Router from "vue-router";
import Frame from "@/components/LayoutPages/Frame";
import Index from "@/components/Pages/Index";
import Products from "@/components/Pages/Products";
import ProductDetail from "@/components/Pages/ProductDetail";
import ProductSearch from "@/components/Pages/ProductSearch";
import Checkout from "@/components/Pages/Checkout";
import ShippingCart from "@/components/Pages/ShippingCart";
import OrderInfo from "@/components/Pages/OrderInfo";
import OrderReviewAndPayment from "@/components/Pages/OrderReviewAndPayment";
//後端
import Dashboard from "@/components/Backend/Dashboard";
import BackendProducts from '@/components/Backend/BackendProducts';
import BackendOrders from '@/components/Backend/BackendOrders';
import BackendCoupon from '@/components/Backend/BackendCoupon';

Vue.use(Router);

export default new Router({
  linkExactActiveClass: "active",
  routes: [
    {
      path: "*",
      redirect: "/"
    },
    {
      path: "/",
      component: Frame,
      children: [
        {
          path: "",
          name: "Index",
          component: Index
        },
        {
          path: "/products/:categoryFromHeader",
          name: "Products",
          component: Products
        },
        {
          path: "/product/:id",
          name: "ProductDetail",
          component: ProductDetail
        },
        {
          path: "/search/:keyword",
          name: "ProductSearch",
          component: ProductSearch
        },
        {
          path: "/checkout/shippingCart",
          component: Checkout,
          children: [
            {
              path: "",
              name: "ShippingCart",
              component: ShippingCart
            },
            {
              path: "/checkout/orderInfo",
              name: "OrderInfo",
              component: OrderInfo
            },
            {
              path: "/checkout/review_payment/:orderId",
              name: "OrderReview",
              component: OrderReviewAndPayment
            }
          ]
        }
      ]
    },
    {
      path: "/admin",
      component: Dashboard,
      meta: { requiresAuth: true },
      children: [
        {
          path: "",
          name: "BackendProducts",
          component: BackendProducts,
          meta: { requiresAuth: true }
        },
        {
          path: 'orders',
          name: 'BackendOrders',
          component: BackendOrders,
          meta: { requiresAuth: true },
        },
        {
          path: 'coupon',
          name: 'BackendCoupon',
          component: BackendCoupon,
          meta: { requiresAuth: true },
        }
      ]
    }
  ]
});



