<template>
  <div
    class="modal fade"
    id="addCartModal"
    tabindex="-1"
    role="dialog"
    aria-labelledby="addCartModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-body">
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>

          <template v-if="modalCategory == '男裝' || modalCategory == '女裝'">
            <div class="form-group mb-4">
              <label for="color">顏色</label>
              <select class="custom-select" id="color">
                <option selected>選擇顏色</option>
                <option value="blue">藍色</option>
                <option value="block">黑色</option>
                <option value="white">白色</option>
                <option value="red">紅色</option>
              </select>
            </div>
            <div class="form-group mb-5">
              <label for="size">尺寸</label>
              <select class="custom-select" id="size">
                <option selected>選擇尺寸</option>
                <option value="1">S</option>
                <option value="2">M</option>
                <option value="3">L</option>
                <option value="3">XL</option>
              </select>
            </div>
          </template>
          <div class="form-row">
            <div class="form-group col-md-4">
              <div class="input-group mb-3">
                <div class="input-group-prepend">
                  <button
                    class="btn btn-outline-secondary"
                    type="button"
                    @click.prevent="qtyChange(-1)"
                  >
                    <i class="fas fa-minus"></i>
                  </button>
                </div>
                <input
                  type="text"
                  class="form-control"
                  placeholder="請輸入購買數量"
                  v-model="addCartQty"
                  name="num"
                  id="num"
                  @keyup="checkNum"
                />
                <div class="input-group-append">
                  <button
                    class="btn btn-outline-secondary"
                    type="button"
                    @click.prevent="qtyChange(1)"
                  >
                    <i class="fas fa-plus"></i>
                  </button>
                </div>
              </div>
              <small class="form-text text-muted">
                庫存狀況：
                <strong class="text-pink">有貨</strong>
              </small>
            </div>
            <div class="form-group col-md-8">
              <button
                type="button"
                id="addToCart-btn"
                class="btn btn-block btn-cyan addToCart-btn"
                @click="addToCart"
              >
                <i class="fas fa-spinner fa-spin" v-if="addCartLoading"></i>
                加到購物車
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["addCartPrdId", "modalCategory"],
  data() {
    return {
      addCartQty: 1,
      addCartLoading: false
    };
  },
  methods: {
    qtyChange(num) {
      const vm = this;
      const newNum = Number(vm.addCartQty) + num;
      if (newNum > 10) {
        vm.addCartQty = 10;
      } else if (newNum < 1) {
        vm.addCartQty = 1;
      } else {
        vm.addCartQty = newNum;
      }
    },
    addToCart() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const cart = {
        product_id: vm.addCartPrdId,
        qty: Number(vm.addCartQty)
      };
      document.querySelector(".addToCart-btn").setAttribute("disabled", "");
      vm.addCartLoading = true;
      this.$http.post(api, { data: cart }).then(response => {
        vm.addCartLoading = false;
        $("#addCartModal").modal("hide");
        if (response.data.success) {
          this.$bus.$emit("showMessage", "商品成功加入購物車！", "success");
        } else {
          this.$bus.$emit("showMessage", response.data.message, "danger");
        }
        this.$bus.$emit("cartReload");
      });
    },
    checkNum() {
      const vm = this;
      if (isNaN(Number(vm.addCartQty))) {
        vm.addCartQty = 1;
        this.$bus.$emit("showMessage", "購買數量請輸入數字", "danger");
      }
    }
  },
  updated() {
    const vm = this;
    $("#addCartModal").on("hidden.bs.modal", function(e) {
      vm.addCartQty = 1;
      document.querySelector(".addToCart-btn").removeAttribute("disabled");
    });
  }
};
</script>



