<template>
  <nav class="navbar navbar-expand-lg navbar-light bg-light">
    <div class="container">
      <router-link class="navbar-brand" to="/">
        <img
          src="../../assets/logo.png"
          style="width: 30px; height: 30px"
          class="d-inline-block align-top"
          alt="Vue 好購購"
        />
        好購購
      </router-link>
      <button
        class="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse justify-content-end" id="navbarSupportedContent">
        <ul class="navbar-nav">
          <li class="nav-item mx-2">
            <router-link class="nav-link" to="/">HOME</router-link>
          </li>
          <li class="nav-item dropdown mx-2">
            <a
              class="nav-link dropdown-toggle"
              href="#"
              id="navbarDropdown"
              role="button"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >PRODUCTS</a>
            <div class="dropdown-menu" aria-labelledby="navbarDropdown">
              <router-link class="dropdown-item" :to="{name: 'Products', params: {'categoryFromHeader': 'all'}}">所有商品</router-link>
              <template v-for="item in getCategoryList">
                <router-link
                  :key="item"
                  :to="{name: 'Products', params: {'categoryFromHeader': item}}"
                  class="dropdown-item"
                >{{item}}</router-link>
              </template>
            </div>
          </li>
        </ul>
        <form class="form-inline my-2 my-lg-0">
          <div class="input-group">
            <input
              type="search"
              class="form-control"
              placeholder="搜尋"
              aria-label="Search"
              v-model="keyWord"
              @keyup.enter="searchProduct"
            />
            <div class="input-group-append">
              <button class="btn btn-outline-cyan" type="button" @click="searchProduct">
                <i class="fas fa-search"></i>
              </button>
            </div>
          </div>
        </form>
        <ul class="navbar-nav d-flex">
          <li class="nav-item dropdown" v-if="closePage === false">
            <a
              class="nav-link dropdown-toggle px-3"
              href="#"
              role="button"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >
              <i class="fas fa-shopping-cart"></i>
              <span class="badge badge-light">{{cartQty}}</span>
            </a>
            <Cart @cartQty="cartQtyShow" />
          </li>

        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
import Cart from "../pages/Cart";

export default {
  components: {
    Cart
  },
  data() {
    return {
      keyWord: "",
      cartQty: 0,
      allProducts: [],
      closePage: false
    };
  },
  methods: {
    getAllProducts() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products`;
      this.$http.get(api).then(response => {
        vm.allProducts = response.data.products.filter(item => {
          return item.is_enabled == 1;
        });
      });
    },
    searchProduct() {
      const vm = this;
      vm.$router.push({
        name: "ProductSearch",
        params: { keyword: vm.keyWord }
      });
      vm.keyWord = "";
    },
    cartQtyShow(qty) {
      const vm = this;
      vm.cartQty = qty;
    }
  },
  computed: {
    getCategoryList() {
      const vm = this;
      let category = [];
      this.allProducts.map(item => {
        category.push(item.category);
      });
      const categpry_return = category.filter((item, index, arr) => {
        return arr.indexOf(item) === index;
      });
      vm.$emit("footerProdNav", categpry_return);
      return categpry_return;
    }
  },
  created() {
    this.getAllProducts();
    this.$bus.$on("cartClose", (change) => {
      this.closePage = change;
    });
  }
};
</script>



