<template>
  <div class="container mb-5">
    <loading :active.sync="isLoading"></loading>
    <div class="page-title">
      <h1 class="display-5 mb-2">{{category == 'all' ? '所有商品' : category}}</h1>
      <div class="lead">
        <Breadcrumb :clickCategory="category" :page="'products'" @productSort="sortChange" />
      </div>
      <hr class="mb-0" />
    </div>
    <div class="row">
      <div
        id="sidebar"
        class="col-lg-3 col-md-4 sidebar sidebar-left sidebar-animate sidebar-md-show"
      >
        <div class="sidebar-block">
          <p class="h5 mb-2 pb-2">
            <i class="fas fa-tags" style="font-size:14px"></i> 商品標籤
          </p>
          <ul class="nav flex-column">
            <li class="nav-item">
              <router-link
                :to="{name: 'Products', params: {'categoryFromHeader': 'all'}}"
                class="nav-link"
              >所有商品</router-link>
            </li>
            <li class="nav-item" v-for="item in categoryArray" :key="item">
              <router-link
                :to="{name: 'Products', params: {'categoryFromHeader': item}}"
                class="nav-link"
              >{{item}}</router-link>
            </li>
          </ul>
        </div>
      </div>
      <div class="col-lg-9 col-md-12">
        <div class="row mb-4">
          <div class="col-12 d-flex justify-content-between">
            <div>
              <button
                type="button"
                class="btn btn-sm btn-outline-cyan sidbar-open-btn"
                data-toggle="sidebar"
                data-target=".sidebar-left"
              >開啟側欄</button>
            </div>
            <div>
              <select class="custom-select custom-select-sm" v-model="sort">
                <option value selected>商品排序</option>
                <option value="desc">按價格排序：由高到低</option>
                <option value="asc">按價格排序：由低到高</option>
              </select>
            </div>
          </div>
        </div>
        <div class="row mb-4">
          <div class="col-lg-4 col-md-6 col-12 mb-4" v-for="item in productsShow" :key="item.id">
            <ProductCard :product="item" @addToCart="openAddCartModal" />
          </div>
        </div>
        <div class="row" v-if="category === 'all'">
          <div class="col-12">
            <Pagination :pagination="pagination" @pageChange="getPageProducts" />
          </div>
        </div>
      </div>
    </div>
    <AddToCardModal :addCartPrdId="addCartPrdId" :modalCategory="modalCategory" />
  </div>
</template>

<script>
import Breadcrumb from "../Breadcrumb";
import ProductCard from "../ProductCard";
import Pagination from "../Pagination";
import AddToCardModal from "../AddToCardModal";
import "bootstrap-sidebar";

export default {
  components: {
    Breadcrumb,
    ProductCard,
    Pagination,
    AddToCardModal
  },
  data() {
    return {
      allProducts: [], //所有商品
      pageProducts: [], //分頁商品
      pagination: {}, //分頁資料
      category: "all", //選擇的標籤 (顯示全部使用 All 關鍵字)
      sort: "", //商品排序
      isLoading: false, //讀取畫面
      addCartPrdId: "",
      modalCategory: "",
      addCartLoading: false
    };
  },
  methods: {
    getAllProducts() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products`;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        vm.allProducts = response.data.products.filter(item => {
          return item.is_enabled == 1;
        });
        vm.isLoading = false;
      });
    },
    getPageProducts(page = 1) {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        vm.pageProducts = response.data.products.filter(item => {
          return item.is_enabled == 1;
        });
        vm.isLoading = false;
        vm.pagination = response.data.pagination;
      });
    },
    sortChange(value) {
      const vm = this;
      vm.sort = value;
    },
    openAddCartModal(productId, selectedCategory) {
      const vm = this;
      vm.addCartPrdId = productId;
      vm.modalCategory = selectedCategory;
      $("#addCartModal").modal("show");
    },
    qtyChange(num) {
      const vm = this;
      const newNum = Number(vm.addCartQty) + num;
      if (newNum > 10) {
        vm.addCartQty = 10;
      } else if (newNum < 1) {
        vm.addCartQty = 1;
      } else {
        vm.addCartQty = newNum;
      }
    }
  },
  computed: {
    productsShow() {
      const vm = this;
      let category_temp = [];
      if (vm.category === "all") {
        category_temp = vm.pageProducts;
      } else {
        category_temp = vm.allProducts.filter(item => {
          return item.category === vm.category;
        });
      }
      if (vm.sort != "") {
        return category_temp.sort(function(a, b) {
          return vm.sort === "asc"
            ? a["origin_price"] > b["origin_price"]
            : a["origin_price"] < b["origin_price"];
        });
      } else {
        return category_temp;
      }
    },
    categoryArray() {
      const vm = this;
      let category = [];
      vm.allProducts.map(item => {
        category.push(item.category);
      });
      return category.filter((item, index, arr) => {
        return arr.indexOf(item) === index;
      });
    }
  },
  watch: {
    $route(to, from) {
      const vm = this;
      this.category = to.params.categoryFromHeader;
    },
    sort(newVel, oldVel) {
      const vm = this;
      this.sort = newVel;
    }
  },
  created() {
    if (this.$route.params.categoryFromHeader != undefined) {
      this.category = this.$route.params.categoryFromHeader;
    }
    this.getAllProducts();
    this.getPageProducts();
    this.$bus.$emit("cartClose", false);
  }
};
</script>

<style scoped>
.page-title {
  padding: 2.5rem 0;
}

.lead nav {
  font-size: 80%;
  font-weight: 400;
}

.sidbar-open-btn {
  display: none !important;
}

/* sidebar settings */
.sidebar {
  display: block;
  top: 0;
  bottom: 0;
  overflow: auto;
}
@media (max-width: 991.98px) {
  .sidbar-open-btn {
    display: inline-block !important;
  }
  .sidebar {
    position: fixed !important;
    width: 300px !important;
    z-index: 1000;
    background: #f8f9fa;
    padding: 2rem;
  }
}

.sidebar-left {
  left: 0;
}

.sidebar-right {
  right: 0;
}

.sidebar-block .h5 {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.sidebar a {
  position: relative;
}

.sidebar a.active {
  color: #0f6674;
}

.sidebar a.active::after {
  font-weight: 900;
  font-family: "Font Awesome 5 Free";
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  line-height: 1;
  content: "\f00c";
  position: absolute;
  right: 20px;
  top: 12px;
}

/* css to override hiding the sidebar according to different screen sizes */
.row .sidebar.sidebar-left.sidebar-xs-show {
  left: 0;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  -moz-transform: none;
}

@media (min-width: 768px) {
  .row .sidebar.sidebar-left.sidebar-sm-show {
    left: 0;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    -moz-transform: none;
  }
}

@media (min-width: 992px) {
  .row .sidebar.sidebar-left.sidebar-md-show {
    left: 0;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    -moz-transform: none;
  }
}

@media (min-width: 1170px) {
  .row .sidebar.sidebar-left.sidebar-lg-show {
    left: 0;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    -moz-transform: none;
  }
}

/* animation class - optional: without it the sidebar would just pop in and out*/
.sidebar-animate {
  -webkit-transition: -webkit-transform 300ms ease;
  -moz-transition: -moz-transform 300ms ease;
  transition: transform 300ms ease;
}

/* Left panel positioning classes */
.sidebar.sidebar-left {
  -webkit-transform: translate3d(-100%, 0, 0);
  -moz-transform: translate3d(-100%, 0, 0);
  transform: translate3d(-100%, 0, 0);
}

.sidebar.sidebar-left.sidebar-open {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  -moz-transform: none;
}
</style>



