<template>
  <main>
    <div class="jumbotron jumbotron-fluid w-100 d-flex justify-content-center">
      <div class="container m-4 p-4">
        <h1 class="display-4">新開幕</h1>
        <p class="lead">最新潮流服飾配件</p>
        <hr class="my-2" />
        <p>全館免運，現正實施中</p>
        <router-link to="/products/all" class="btn btn-cyan btn-lg">購物去</router-link>
      </div>
    </div>
    <div class="container">
      <div class="row">
        <div class="col-12 mb-3">
          <h4 class="font-weight-bolder mb-0 text-uppercase">Category</h4>
        </div>
      </div>
      <div class="row mb-5">
        <div class="col-lg-3 col-md-6 col-12 mb-4">
          <div class="card bg-dark text-white category-card">
            <img src="../../assets/category-1.jpg" class="card-img" alt="男裝" />
            <router-link
              :to="{name: 'Products', params: {'categoryFromHeader': '男裝'}}"
              class="card-img-overlay d-flex justify-content-center mb-0"
            >男裝</router-link>
          </div>
        </div>
        <div class="col-lg-3 col-md-6 col-12 mb-4">
          <div class="card bg-dark text-white category-card">
            <img src="../../assets/category-2.jpg" class="card-img" alt="女裝" />
            <router-link
              :to="{name: 'Products', params: {'categoryFromHeader': '女裝'}}"
              class="card-img-overlay d-flex justify-content-center mb-0"
            >女裝</router-link>
          </div>
        </div>
        <div class="col-lg-3 col-md-6 col-12 mb-4">
          <div class="card bg-dark text-white category-card">
            <img src="../../assets/category-2.jpg" class="card-img" alt="女裝" />
            <router-link
              :to="{name: 'Products', params: {'categoryFromHeader': '女裝'}}"
              class="card-img-overlay d-flex justify-content-center mb-0"
            >女裝</router-link>
          </div>
        </div>
        <div class="col-lg-3 col-md-6 col-12 mb-4">
          <div class="card bg-dark text-white category-card">
            <img src="../../assets/category-2.jpg" class="card-img" alt="女裝" />
            <router-link
              :to="{name: 'Products', params: {'categoryFromHeader': '女裝'}}"
              class="card-img-overlay d-flex justify-content-center mb-0"
            >女裝</router-link>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-12 mb-3">
          <h4 class="font-weight-bolder mb-0 text-uppercase">New Products</h4>
        </div>
      </div>
      <div class="row mb-4">
        <div class="row mb-4">
          <div class="col-lg-3 col-md-6 col-12 mb-4" v-for="item in newProducts" :key="item.id">
            <ProductCard :product="item" @addToCart="openAddCartModal" />
          </div>
        </div>
      </div>
    </div>
    <AddToCardModal :addCartPrdId="addCartPrdId" :modalCategory="modalCategory" />
  </main>
</template>

<script>
import ProductCard from "../ProductCard";
import AddToCardModal from "../AddToCardModal";

export default {
  components: {
    ProductCard,
    AddToCardModal
  },
  data() {
    return {
      allProducts: [],
      addCartPrdId: "",
      modalCategory: "",
    };
  },
  methods: {
    getAllProducts() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products`;
      this.$http.get(api).then(response => {
        vm.allProducts = response.data.products.filter(item => {
          return item.is_enabled == 1;
        });
      });
    },
    openAddCartModal(productId, selectedCategory) {
      const vm = this;
      vm.addCartPrdId = productId;
      vm.modalCategory = selectedCategory;
      $("#addCartModal").modal("show");
    }
  },
  computed: {
    newProducts() {
      const vm = this;
      let returnProducts = [];
      for (let i = 0; i < 8; i++) {
        if (vm.allProducts[i] != undefined) {
          returnProducts.push(vm.allProducts[i]);
        }
      }
      return returnProducts;
    }
  },
  created() {
    this.$bus.$emit("cartClose", false);
    this.getAllProducts();
  }
};
</script>

<style scoped>
.jumbotron {
  background-image: url("../../assets/index.jpg");
  background-position: center center;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  height: 710px;
  align-items: center;
}
.jumbotron .container {
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 10px;
  width: 520px;
  height: 267px;
}
.category-card {
  border: none;
  overflow: hidden;
}
.category-card .card-img {
  transition: transform 0.2s;
}
.category-card:hover .card-img {
  transform: scale(1.5);
}
.category-card .card-img-overlay {
  align-items: center;
  flex-direction: column;
  font-weight: 900;
  color: white;
  background-color: rgba(255, 255, 255, 0.3);
  font-size: 1.94rem;
}
.category-card .card-img-overlay:hover {
  text-decoration: none;
}
</style>



