<template>
  <div class="card shadow-sm">
    <router-link :to="{name: 'ProductDetail', params: {id : product.id}}">
      <img class="card-img-top" :src="product.imageUrl" :alt="product.title" />
    </router-link>
    <div class="card-body">
      <h5 class="card-title">
        <router-link :to="{name: 'ProductDetail', params: {'id' : product.id}}" class="card-link text-dark">
          <span class="badge badge-pill badge-warning">New</span>
          {{product.title}}
        </router-link>
      </h5>
      <p class="card-text" v-html="product.content"></p>
      <p class="card-text mb-0 clearfix">
        <template v-if="product.price != product.origin_price">
          <del class="h6 text-secondary" v-if="product.price">{{ product.price | currency }} 元</del>
          <strong class="h6 text-cyan ml-1" >{{ product.origin_price | currency }} 元</strong>
        </template>
        <template v-else>
          <strong class="h6 text-cyan ml-1">{{ product.price | currency }} 元</strong>
        </template>
        <button type="button" class="btn btn-sm btn-outline-cyan float-right" @click="addToCart(product.id, product.category)">
          <i class="fas fa-cart-plus"></i>
        </button>
      </p>
    </div>
  </div>
</template>

<script>
export default {
    props: ['product'],
    methods: {
      addToCart(productId, category) {
        this.$emit('addToCart', productId, category);
      }
    }
};
</script>



