<template>
  <div class="text-gray-dark">
    <loading :active.sync="isLoading"></loading>
    <div class="row mb-4">
      <div class="col-12">
        <p class="h4 pb-2 mb-3" style="border-bottom: 1px solid rgba(0, 0, 0, 0.1)">
          <i class="fas fa-file-alt"></i> 訂單資訊
        </p>
        <div class="row">
          <div class="col-lg-8 col-md-12 mb-4">
            <div class="table-responsive-md">
              <table class="table">
                <thead>
                  <tr>
                    <th>商品</th>
                    <th class="text-center">數量</th>
                    <th class="text-center">小計</th>
                    <th class="text-center">折扣價</th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="item in order.products" :key="item.id">
                    <td class="align-middle">
                      <div class="product-item">
                        <router-link
                          :to="{name: 'ProductDetail', params: {'id' : item.product.id}}"
                          class="product-thumb"
                        >
                          <img :src="item.product.imageUrl" alt />
                        </router-link>
                        <div class="product-info">
                          <p class="proudct-title">
                            <router-link
                              :to="{name: 'ProductDetail', params: {'id' : item.product.id}}"
                            >{{item.product.title}}</router-link>
                          </p>
                          <p class="mb-0 small">
                            <strong>Size:</strong> L
                          </p>
                          <p class="mb-0 small">
                            <strong>Color:</strong> Green
                          </p>
                        </div>
                      </div>
                    </td>
                    <td class="text-center">{{ item.qty }}</td>
                    <td class="text-center font-weight-bold">{{ item.total | currency }}</td>
                    <td
                      class="text-center font-weight-bold"
                    >{{ (item.final_total != item.total) ? (item.final_total | currency) : "—" }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          <div class="col-lg-4 col-md-12">
            <div class="card mb-4">
              <div class="card-header">結帳金額</div>
              <div class="card-body">
                <p class="card-text h4 text-center">{{order.total | currency}}</p>
              </div>
              <div class="card-footer">
                <span v-if="!order.is_paid">尚未付款</span>
                <span v-else class="text-success">付款完成</span>
              </div>
            </div>
            <div class="card cart-info-card">
              <div class="card-header">收件人資訊</div>
              <div class="card-body">
                <p class="card-text">
                  <span>收件人姓名：</span>
                  <span>{{ order.user.name }}</span>
                </p>
                <p class="card-text">
                  <span>Email：</span>
                  <span>{{ order.user.email }}</span>
                </p>
                <p class="card-text">
                  <span>收件人電話：</span>
                  <span>{{ order.user.tel }}</span>
                </p>
                <p class="card-text">
                  <span>收件人地址：</span>
                  <span>{{ order.user.address }}</span>
                </p>
                <div>
                  <p>訊息：</p>
                  <p class="mb-0">{{ order.message }}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="row" v-if="order.is_paid === false">
      <div class="col-12">
        <p class="h4 pb-2 mb-3" style="border-bottom: 1px solid rgba(0, 0, 0, 0.1)">
          <i class="fas fa-file-alt"></i> 付款
        </p>
        <div class="row d-flex justify-content-center">
          <div class="col-lg-6 col-md-12">
            <div class="card">
              <div class="card-header">信用卡資訊</div>
              <div class="card-body">
                <p class="card-tex">
                  提供下列信用卡交易：
                  <i class="fab fa-cc-visa"></i>
                  <i class="fab fa-cc-mastercard"></i>
                  <i class="fab fa-cc-jcb"></i>
                </p>
                <form @submit.prevent="payOrder">
                  <div class="form-group">
                    <label for="card-name">持卡人姓名</label>
                    <input
                      type="text"
                      class="form-control"
                      id="card-name"
                      name="card-name"
                      v-validate="'required'"
                      :class="{'is-invalid' : errors.has('card-name')}"
                      placeholder="持卡人姓名"
                    />
                    <span
                      class="text-danger"
                      v-if="errors.has('card-name')"
                    >{{ errors.first('card-name').replace(/card-name/,'持卡人姓名') }}</span>
                  </div>
                  <div class="form-group">
                    <label for="card-number">信用卡號</label>
                    <input
                      type="text"
                      class="form-control"
                      id="card-number"
                      name="card-number"
                      placeholder="輸入信用卡號"
                      v-validate="'required|credit_card'"
                      :class="{'is-invalid' : errors.has('card-number')}"
                    />
                    <span
                      class="text-danger"
                      v-if="errors.has('card-number')"
                    >{{ errors.first('card-number').replace(/card-number/,'信用卡卡號') }}</span>
                  </div>
                  <div class="form-group">
                    <label for="card-date">有效期限</label>
                    <input
                      type="text"
                      class="form-control"
                      id="card-date"
                      name="card-date"
                      v-validate="'required|digits:4'"
                      :class="{'is-invalid' : errors.has('card-date')}"
                      placeholder="範例：0825"
                    />
                    <span
                      class="text-danger"
                      v-if="errors.has('card-date')"
                    >{{ errors.first('card-date').replace(/card-date/,'有效期限') }}</span>
                  </div>
                  <div class="form-group">
                    <label for="card-CVC">卡片後三碼</label>
                    <input
                      type="text"
                      class="form-control"
                      id="card-CVC"
                      name="card-CVC"
                      v-validate="'required|digits:3'"
                      :class="{'is-invalid' : errors.has('card-CVC')}"
                      placeholder="卡片後三碼"
                    />
                    <span
                      class="text-danger"
                      v-if="errors.has('card-CVC')"
                    >{{ errors.first('card-CVC').replace(/card-CVC/,'卡片後三碼') }}</span>
                  </div>
                  <div class="form-group mb-0 d-flex justify-content-between">
                    <button type="reset" class="btn btn-secondary" @click="resetErrors">清除重填</button>
                    <button type="submit" class="btn btn-cyan">確認付款</button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      orderId: "",
      order: {
        user: {}
      },
      isLoading: false
    };
  },
  methods: {
    getOrder() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${vm.orderId}`;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        vm.order = response.data.order;
        vm.isLoading = false;
      });
    },
    resetErrors() {
      const vm = this;
      vm.errors.clear();
    },
    payOrder() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${vm.orderId}`;
      vm.isLoading = true;
      this.$http.post(api).then(response => {
        console.log(response);
        if (response.data.success) {
          this.$bus.$emit("showMessage", response.data.message, "success");
          vm.getOrder();
        }
        vm.isLoading = false;
      });
    }
  },
  created() {
    this.$emit("breadData", "訂單確認 & 付款");
    if (this.$route.params.orderId != undefined) {
      this.orderId = this.$route.params.orderId;
      this.getOrder();
    } else {
      this.$router.push({ name: "ShippingCart" });
    }
  }
};
</script>

<style scoped>
.table th,
.table td {
  vertical-align: middle !important;
}
.table th {
  border-top: none;
}
.product-item {
  display: table;
  width: 100%;
  min-width: 150px;
  margin-top: 5px;
  margin-bottom: 3px;
}
.count-input {
  display: inline-block;
  width: 86px;
}
.product-item .product-thumb {
  display: table-cell;
  vertical-align: top;
  width: 110px;
  padding-right: 20px;
}
.product-item .product-thumb > img {
  display: block;
  width: 100%;
  max-width: 100%;
  height: auto;
}
.product-item .product-info {
  display: table-cell;
  vertical-align: top;
}
.product-info .proudct-title {
  margin-bottom: 6px;
  padding-top: 5px;
  font-size: 16px;
  font-weight: 500;
}
.cart-info-card .card-text {
  display: flex !important;
  -webkit-box-pack: justify !important;
  -ms-flex-pack: justify !important;
  justify-content: space-between !important;
}
</style>



