<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mt-4">
      <button class="btn btn-primary" @click="openModal(true)">建立新優惠券</button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th>名稱</th>
          <th>折扣百分比</th>
          <th>到期日</th>
          <th>是否啟用</th>
          <th>編輯</th>
        </tr>
      </thead>
      <tbody v-if="coupons.length > 0">
        <tr v-for="coupon in coupons" :key="coupon.id">
          <td>{{coupon.title}}</td>
          <td>{{coupon.percent | percent}}</td>
          <td>{{coupon.due_date}}</td>
          <td>
            <span class="text-success" v-if="coupon.is_enabled">啟用</span>
            <span v-else>未啟用</span>
          </td>
          <td>
            <div class="btn-group btn-group-sm" role="group" aria-label="...">
              <button class="btn btn-outline-primary btn-sm" @click="openModal(false, coupon)">編輯</button>
              <button class="btn btn-outline-danger btn-sm" @click="openDelModal(coupon)">刪除</button>
            </div>
          </td>
        </tr>
      </tbody>
      <tbody v-else><tr><td colspan="5" class="text-center"><strong>目前沒有優惠券</strong></td></tr></tbody>
    </table>

    <Pagination :pagination="pagination" @pageChange="getCoupons" />

    <div
      class="modal fade"
      id="couponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="couponModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="couponModalLabel">{{(isNew) ? "建立新" : "編輯"}}優惠券</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="title">標題</label>
              <input
                type="text"
                class="form-control"
                id="title"
                placeholder="請輸入標題"
                v-model="tempCoupon.title"
              />
            </div>
            <div class="form-group">
              <label for="code">優惠碼</label>
              <input
                type="text"
                class="form-control"
                id="code"
                placeholder="請輸入優惠碼"
                v-model="tempCoupon.code"
              />
            </div>
            <div class="form-group">
              <label for="dueDate">到期日</label>
              <input
                type="date"
                class="form-control"
                id="dueDate"
                placeholder="請輸入到期日"
                v-model="tempCoupon.due_date"
              />
            </div>
            <div class="form-group">
              <label for="percent">折扣百分比</label>
              <div class="input-group">
                <input
                  type="number"
                  class="form-control"
                  id="percent"
                  placeholder="請輸入折扣百分比"
                  v-model="tempCoupon.percent"
                />
                <div class="input-group-prepend">
                  <div class="input-group-text">%</div>
                </div>
              </div>
            </div>
            <div class="form-group form-check mb-0">
              <input
                type="checkbox"
                class="form-check-input"
                id="is_enabled"
                v-model="tempCoupon.is_enabled"
                :true-value="1"
                :false-value="0"
              />
              <label class="form-check-label" for="is_enabled">是否啟用</label>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">關閉</button>
            <button
              type="button"
              class="btn btn-primary"
              @click.prevent="updateCoupon"
            >{{(isNew) ? "建立" : "更新"}}優惠券</button>
          </div>
        </div>
      </div>
    </div>

    <div
      class="modal fade"
      id="delCouponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="delCouponModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="delCouponModalLabel">刪除優惠券</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            是否刪除
            <strong class="text-danger">{{ tempCoupon.title }}</strong> 優惠券(刪除後將無法恢復)？
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" @click="deleteCoupon">確認刪除</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Pagination from "../Pagination";

export default {
  components: {
    Pagination
  },
  data() {
    return {
      coupons: [],
      pagination: {},
      tempCoupon: {},
      isNew: false,
      isLoading: false
    };
  },
  methods: {
    getCoupons(page = 1) {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        vm.coupons = response.data.coupons;
        vm.isLoading = false;
        vm.pagination = response.data.pagination;
      });
    },
    updateCoupon() {
      const vm = this;
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
      let httpMethod = "post";
      if (!vm.isNew) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
        httpMethod = "put";
      }
      this.$http[httpMethod](api, { data: vm.tempCoupon }).then(response => {
        $("#couponModal").modal("hide");
        vm.getCoupons();
        if (response.data.success) {
          this.$bus.$emit("message:push", response.data.message, "success");
        } else {
          this.$bus.$emit("message:push", response.data.message, "danger");
        }
      });
    },
    deleteCoupon() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
      this.$http.delete(api).then(response => {
        $("#delCouponModal").modal("hide");
        vm.getCoupons();
        if (response.data.success) {
          this.$bus.$emit("message:push", response.data.message, "success");
        } else {
          this.$bus.$emit("message:push", response.data.message, "danger");
        }
      });
    },
    openModal(isNew, coupon) {
      if (isNew) {
        this.tempCoupon = {};
        this.isNew = true;
      } else {
        this.tempCoupon = Object.assign({}, coupon);
        this.isNew = false;
      }
      $("#couponModal").modal("show");
    },
    openDelModal(coupon) {
      this.tempCoupon = Object.assign({}, coupon);
      $("#delCouponModal").modal("show");
    }
  },
  created() {
    this.getCoupons();
  }
};
</script>



