<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <table class="table mt-4">
      <thead>
        <tr>
          <th>購買時間</th>
          <th>Email</th>
          <th>訂單內容</th>
          <th>應付金額</th>
          <th>是否付款</th>
        </tr>
      </thead>
      <tbody>
        <template v-if="pagination.total_pages > 0">
          <tr v-for="(item, key) in orders" :key="key">
            <td>{{ item.create_at | timeFormate}}</td>
            <td>{{item.user.email}}</td>
            <td>
              <p v-for="(product, pKey) in item.products" :key="pKey" class="mb-2">
                <span>{{product.product.title}}</span>
                <span>數量：{{product.qty}} / {{product.product.unit}}</span>
              </p>
            </td>
            <td class="text-right">{{item.total | currency}}</td>
            <td>
              <span class="text-success" v-if="item.is_paid">已付款</span>
              <span v-else>尚未付款</span>
            </td>
          </tr>
        </template>
        <template v-else>
          <tr>
            <td colspan="5" class="text-center">
              <strong>目前沒有任何訂單</strong>
            </td>
          </tr>
        </template>
      </tbody>
    </table>

    <Pagination :pagination="pagination" @pageChange="getOrders" />
  </div>
</template>

<script>
import Pagination from "../Pagination";

export default {
  components: {
    Pagination
  },
  data() {
    return {
      orders: [],
      pagination: {},
      isLoading: false
    };
  },
  methods: {
    getOrders(page = 1) {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/orders?page=${page}`;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        vm.orders = response.data.orders;
        vm.pagination = response.data.pagination;
        vm.isLoading = false;
      });
    }
  },
  created() {
    this.getOrders();
  }
};
</script>



