<template>
  <div class="container mb-5">
    <div class="page-title">
      <h1 class="display-5 mb-2">{{breadTitle}}</h1>
      <div class="lead">
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb mb-0 p-0">
            <li
              class="breadcrumb-item"
              :class="{'active': breadTitle == '購物車'}"
              aria-current="page"
            >
              <i class="fas fa-shopping-cart"></i> 購物車
            </li>
            <li
              class="breadcrumb-item"
              :class="{'active': breadTitle == '訂單資訊'}"
              aria-current="page"
            >
              <i class="fas fa-file-signature"></i> 訂單資訊
            </li>
            <li
              class="breadcrumb-item"
              :class="{'active': breadTitle == '訂單確認 & 付款'}"
              aria-current="page"
            >
              <i class="fas fa-check-circle"></i> 訂單確認 & 付款
            </li>
          </ol>
        </nav>
      </div>
      <hr class="mb-0" />
    </div>
    <router-view @breadData="changeBread" />
  </div>
</template>

<script>
import ShippingCart from "./ShippingCart";

export default {
  components: {
    ShippingCart
  },
  data() {
    return {
      breadTitle: "購物車"
    };
  },
  methods: {
    changeBread(newBreadTitle) {
      const vm = this;
      vm.breadTitle = newBreadTitle;
    }
  },
  created() {
    this.$bus.$emit("cartClose", true);
  }
};
</script>

<style scoped>
.page-title {
  padding: 2.5rem 0;
}
.lead nav {
  font-size: 80%;
  font-weight: 400;
}
.breadcrumb {
  background-color: white !important;
}
.breadcrumb-item {
  color: #6c757d;
}
.breadcrumb-item.active {
  color: #17a2b8;
}
.breadcrumb-item + .breadcrumb-item::before {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  content: "\f105";
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  line-height: 1;
}
</style>



