<template>
  <div class="container mt-4 mb-5">
    <loading :active.sync="isLoading"></loading>
    <div class="row justify-content-between product-show">
      <div class="col-md-12 col-lg-6">
        <img :src="product.imageUrl" :alt="product.title" class="img-thumbnail" />
      </div>
      <div class="col-md-12 col-lg-5">
        <p class="mb-4 p-bread">
          <Breadcrumb :clickCategory="breadCategory" :page="'detail'" />
        </p>
        <p class="h3 mb-4">{{product.title}}</p>
        <p class="mb-4">{{product.description}}</p>
        <p class="h3 mb-4">
          <template v-if="product.price != product.origin_price">
            <del class="text-secondary" v-if="product.price">{{ product.price | currency }} 元</del>
            <strong class="text-cyan ml-1">{{ product.origin_price | currency }} 元</strong>
          </template>
          <template v-else>
            <strong class="text-cyan ml-1">{{ product.price | currency }} 元</strong>
          </template>
        </p>
        <template v-if="product.category == '男裝' || product.category == '女裝'">
          <div class="form-group mb-4">
            <label for="color">顏色</label>
            <select class="custom-select" id="color">
              <option selected>選擇顏色</option>
              <option value="blue">藍色</option>
              <option value="block">黑色</option>
              <option value="white">白色</option>
              <option value="red">紅色</option>
            </select>
          </div>
          <div class="form-group mb-5">
            <label for="size">尺寸</label>
            <select class="custom-select" id="size">
              <option selected>選擇尺寸</option>
              <option value="1">S</option>
              <option value="2">M</option>
              <option value="3">L</option>
              <option value="3">XL</option>
            </select>
          </div>
        </template>
        <div class="form-row">
          <div class="form-group col-md-4">
            <div class="input-group mb-3">
              <div class="input-group-prepend">
                <button
                  class="btn btn-outline-secondary"
                  type="button"
                  @click.prevent="qtyChange(-1)"
                >
                  <i class="fas fa-minus"></i>
                </button>
              </div>
              <input
                type="text"
                class="form-control"
                placeholder="請輸入購買數量"
                v-model="qty"
                name="num"
                id="num"
                @keyup="checkNum"
              />
              <div class="input-group-append">
                <button
                  class="btn btn-outline-secondary"
                  type="button"
                  @click.prevent="qtyChange(1)"
                >
                  <i class="fas fa-plus"></i>
                </button>
              </div>
            </div>
            <small class="form-text text-muted">
              庫存狀況：
              <strong class="text-pink">有貨</strong>
            </small>
          </div>
          <div class="form-group col-md-8">
            <button type="button" class="btn btn-block btn-cyan" @click="addToCart(productId)">加到購物車</button>
          </div>
        </div>
        <p class="mb-4" v-if="product.content" v-html="product.content"></p>
      </div>
    </div>
    <div class="row mt-5" v-if="moreProduct.length != 0">
      <div class="col-12 mb-3">
        <h4 class="mb-0">相關商品</h4>
      </div>
      <div class="col-md-3 col-sm-6" v-for="item in moreProduct" :key="item.id">
        <ProductCard :product="item" />
      </div>
    </div>
  </div>
</template>

<script>
import Breadcrumb from "../Breadcrumb";
import ProductCard from "../ProductCard";

export default {
  components: {
    Breadcrumb,
    ProductCard
  },
  data() {
    return {
      productId: "",
      product: {},
      moreProduct: [],
      breadCategory: "",
      isLoading: false,
      qty: 1
    };
  },
  methods: {
    getProduct() {
      const vm = this;
      const id = vm.productId;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        vm.product = response.data.product;
        vm.breadCategory = response.data.product.category;
        vm.isLoading = false;
      });
    },
    qtyChange(num) {
      const vm = this;
      const newNum = Number(vm.qty) + num;
      if (newNum > 10) {
        vm.qty = 10;
      } else if (newNum < 1) {
        vm.qty = 1;
      } else {
        vm.qty = newNum;
      }
    },
    getMoreProducts(category) {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products`;
      this.$http.get(api).then(response => {
        let categoryProducts = response.data.products.filter(item => {
          return item.category === category;
        });
        let return_array = new Array();
        for (let i = 0; i < 4; i++) {
          if (categoryProducts.length > 0) {
            let arrIndex = Math.floor(Math.random() * categoryProducts.length);
            return_array[i] = categoryProducts[arrIndex];
            categoryProducts.splice(arrIndex, 1);
          } else {
            break;
          }
        }
        vm.moreProduct = return_array.filter(item => {
          return item.id != vm.productId;
        });
      });
    },
    addToCart(id) {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const cart = {
        product_id: id,
        qty: Number(vm.qty)
      };
      vm.isLoading = true;
      this.$http.post(api, { data: cart }).then(response => {
        vm.isLoading = false;
        if (response.data.success) {
          this.$bus.$emit("showMessage", "商品成功加入購物車！", "success");
        } else {
          this.$bus.$emit("showMessage", response.data.message, "danger");
        }
        this.$bus.$emit("cartReload");
        vm.qty = 1;
      });
    },
    checkNum() {
      const vm = this;
      if (isNaN(Number(vm.qty))) {
        vm.qty = 1;
        this.$bus.$emit("showMessage", "購買數量請輸入數字", "danger");
      }
    }
  },
  watch: {
    breadCategory(newVel, oldVel) {
      const vm = this;
      vm.getMoreProducts(newVel);
    },
    $route(to, from) {
      const vm = this;
      vm.productId = to.params.id;
      vm.getProduct();
    }
  },
  created() {
    this.productId = this.$route.params.id;
    this.getProduct();
    this.$bus.$emit("cartClose", false);
  }
};
</script>

<style scoped>
@media (max-width: 767.98px) {
  .product-show div:first-child {
    margin-bottom: 1.5rem !important;
  }
  .input-group-append {
    margin-bottom: 1.5rem !important;
  }
}

.p-bread .bread-nav {
  background-color: white !important;
  padding: 0 !important;
}
</style>



