<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="row text-gray-dark d-flex justify-content-center">
      <div class="col-4 p-5 text-center" v-if="cartEmpty">
        <p class="display-1">
          <i class="fas fa-shopping-cart"></i>
        </p>
        <p class="h3 mb-3">您的購物車中沒有商品</p>
        <router-link
          :to="{name: 'Products', params: {'categoryFromHeader': 'all'}}"
          class="btn btn-cyan btn-block"
        >去購物</router-link>
      </div>
      <div class="col-12" v-else>
        <div class="table-responsive-md mb-2">
          <table class="table">
            <thead>
              <tr>
                <th>商品</th>
                <th class="text-center">數量</th>
                <th class="text-center">小計</th>
                <th class="text-center">折扣價</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in carts.carts" :key="item.id">
                <td>
                  <div class="product-item">
                    <router-link
                      :to="{name: 'ProductDetail', params: {'id' : item.product.id}}"
                      class="product-thumb"
                    >
                      <img :src="item.product.imageUrl" alt />
                    </router-link>
                    <div class="product-info">
                      <p class="proudct-title">
                        <router-link
                          :to="{name: 'ProductDetail', params: {'id' : item.product.id}}"
                        >{{item.product.title}}</router-link>
                      </p>
                      <p class="mb-0 small">
                        <strong>Size:</strong> L
                      </p>
                      <p class="mb-0 small">
                        <strong>Color:</strong> Green
                      </p>
                    </div>
                  </div>
                </td>
                <td class="text-center">
                  <div class="count-input">
                    <select class="custom-select custom-select-sm" v-model="item.qty">
                      <option :value="num" v-for="num in 10" :key="num">{{num}}</option>
                    </select>
                  </div>
                </td>
                <td class="text-center font-weight-bold">{{ item.total | currency }}</td>
                <td
                  class="text-center"
                >{{ (item.final_total == item.total) ? "—" : (item.final_total | currency) }}</td>
                <td class="text-center">
                  <button class="btn btn-outline-danger btn-sm" @click="removeCartItem(item.id)">
                    <i class="far fa-trash-alt"></i>
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
          <div class="cart-footer d-flex justify-content-between">
            <div class="coupon-input form-inline py-2">
              <div class="form-group mx-sm-3 mb-0">
                <input
                  type="text"
                  class="form-control form-control-sm"
                  id="coupon"
                  placeholder="輸入優惠碼"
                  v-model="coupon_code"
                />
              </div>
              <button type="submit" class="btn btn-cyan btn-sm mb-0" @click="addCouponCode">輸入優惠券</button>
            </div>
            <div class="cart-total text-right">
              <p class="mb-0">
                總計:
                <strong
                  :class="{'text-secondary cancel' : carts.final_total !== carts.total}"
                >{{ carts.total | currency }}</strong>
              </p>
              <p class="mb-0" v-if="carts.final_total !== carts.total">
                折扣價:
                <strong>{{ carts.final_total | currency }}</strong>
              </p>
            </div>
          </div>
          <div class="cart-footer d-flex justify-content-between">
            <span>
              <router-link
                :to="{name: 'Products', params: {'categoryFromHeader': 'all'}}"
                class="btn btn-outline-dark"
              >
                <i class="fas fa-angle-left"></i> 繼續購物
              </router-link>
            </span>
            <span>
              <button class="btn btn-outline-primary mr-2" @click="updateCart">更新購物車</button>
              <router-link
                :to="{name: 'OrderInfo', params: {'carts': carts}}"
                class="btn btn-cyan"
              >填寫訂單資訊 <i class="fas fa-angle-right"></i></router-link>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      carts: [],
      coupon_code: "",
      isLoading: false,
      cartEmpty: true
    };
  },
  methods: {
    getCart() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        if (response.data.data.total != 0) {
          vm.carts = response.data.data;
          vm.cartEmpty = false;
        } else {
          vm.cartEmpty = true;
        }
        vm.isLoading = false;
      });
    },
    removeCartItem(id) {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(api).then(response => {
        vm.getCart();
        vm.isLoading = false;
        if (response.data.success) {
          this.$bus.$emit("showMessage", "商品已從購物車移除", "success");
          this.$bus.$emit("cartReload");
        } else {
          this.$bus.$emit("showMessage", response.data.message, "danger");
        }
      });
    },
    addCouponCode() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
      const couponData = {
        code: vm.coupon_code
      };
      vm.isLoading = true;
      this.$http.post(api, { data: couponData }).then(response => {
        if (response.data.success) {
          this.$bus.$emit("showMessage", response.data.message, "success");
          vm.getCart();
        } else {
          vm.isLoading = false;
          this.$bus.$emit("showMessage", response.data.message, "danger");
        }
      });
    },
    updateCart() {
      const vm = this;
      vm.isLoading = true;
      setTimeout(() => {
        vm.isLoading = false;
        this.$bus.$emit("showMessage", "購物車更新成功！", "success");
      }, 2000);
    }
  },
  created() {
    //進入購物車後將 header cart 關閉
    $("#car-dropdown").removeClass("show");
    this.$emit("breadData", "購物車");
    this.getCart();
  }
};
</script>

<style scoped>
.table th,
.table td {
  vertical-align: middle !important;
}
.product-item {
  display: table;
  width: 100%;
  min-width: 150px;
  margin-top: 5px;
  margin-bottom: 3px;
}
.count-input {
  display: inline-block;
  width: 86px;
}
.product-item .product-thumb {
  display: table-cell;
  vertical-align: top;
  width: 110px;
  padding-right: 20px;
}
.product-item .product-thumb > img {
  display: block;
  width: 100%;
  max-width: 100%;
  height: auto;
}
.product-item .product-info {
  display: table-cell;
  vertical-align: top;
}
.product-info .proudct-title {
  margin-bottom: 6px;
  padding-top: 5px;
  font-size: 16px;
  font-weight: 500;
}

.cart-footer {
  display: table;
  width: 100%;
  padding: 10px 0;
  border-top: 1px solid #e1e7ec;
}

.cancel {
  text-decoration: line-through;
}
</style>



