<template>
  <div>
    <Alert />
    <div class="top-bar bg-gray-dark py-2">
      <div class="container">
        <ul class="nav justify-content-end text-white small">
          <li class="nav-item">
            <a
              class="nav-link"
              href="#"
              @click.prevent="openLoginModal"
              v-if="loginLinkClose === false"
            >
              <i class="fas fa-sign-in-alt"></i> 管理員登入
            </a>
            <router-link class="nav-link" to="/admin" v-else>
              <i class="fas fa-sign-in-alt"></i> 進入後台
            </router-link>
          </li>
        </ul>
      </div>
    </div>
    <scroll-fixed-header :fixed.sync="fixed" :threshold="20">
      <Header @footerProdNav="footerNav" />
    </scroll-fixed-header>
    <router-view />
    <Footer :category="categoryForFooter" />

    <div
      class="modal fade"
      id="loginModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="loginModalLabel"
      aria-hidden="true"
      v-if="loginLinkClose === false"
    >
      <div class="modal-dialog modal-sm" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="loginModalLabel">管理員登入</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <form id="signin-form">
              <div class="form-group">
                <label for="email">帳號</label>
                <input
                  type="email"
                  class="form-control"
                  id="signineEmail"
                  name="signineEmail"
                  placeholder="請輸入帳號"
                  required
                  v-model="user.username"
                  v-validate="'required|email'"
                  :class="{'is-invalid' : errors.has('signineEmail')}"
                />
                <span
                  class="text-danger"
                  v-if="errors.has('signineEmail')"
                >{{ errors.first('signineEmail').replace(/signineEmail/,'帳號') }}</span>
              </div>
              <div class="form-group">
                <label for="password">密碼</label>
                <input
                  type="password"
                  class="form-control"
                  id="password"
                  name="password"
                  placeholder="請輸入密碼"
                  required
                  v-model="user.password"
                  v-validate="'required'"
                  :class="{'is-invalid' : errors.has('password')}"
                />
                <span
                  class="text-danger"
                  v-if="errors.has('password')"
                >{{ errors.first('password').replace(/password/,'密碼') }}</span>
              </div>
              <div class="form-row d-flex justify-content-between small mb-3">
                <div class="col-auto">
                  <div class="form-check">
                    <input type="checkbox" class="form-check-input" id="remember" />
                    <label class="form-check-label" for="remember">記住我</label>
                  </div>
                </div>
                <div class="col-auto">
                  <a href="#" role="button">忘記密碼?</a>
                </div>
              </div>
              <div class="form-group mb-0">
                <button
                  type="button"
                  class="btn btn-sm btn-block btn-cyan signin-btn"
                  @click="signin"
                >
                  <i class="fas fa-spinner fa-spin" v-if="addCartLoading"></i> 登入
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from "./Header";
import Footer from "./Footer";
import Alert from "../AlertMessage";

export default {
  components: {
    Header,
    Footer,
    Alert
  },
  data() {
    return {
      fixed: false,
      categoryForFooter: [],
      user: {
        username: "",
        password: ""
      },
      addCartLoading: false,
      loginLinkClose: false
    };
  },
  methods: {
    openLoginModal() {
      $("#loginModal").modal("show");
    },
    footerNav(category) {
      const vm = this;
      vm.categoryForFooter = category;
    },
    signin() {
      const api = `${process.env.APIPATH}/admin/signin`;
      const vm = this;
      document.querySelector(".signin-btn").setAttribute("disabled", "");
      vm.addCartLoading = true;
      this.$http.post(api, vm.user).then(response => {
        vm.addCartLoading = false;
        $("#loginModal").modal("hide");
        if (response.data.success) {
          vm.$router.push("/admin");
        } else {
          vm.$bus.$emit("showMessage", response.data.message, "danger");
        }
      });
    },
    userCheck() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/user/check`;
      this.$http.post(api).then(response => {
        if (response.data.success) {
          vm.loginLinkClose = true;
        } else {
          vm.loginLinkClose = false;
        }
      });
    }
  },
  updated() {
    const vm = this;
    $("#loginModal").on("hidden.bs.modal", function(e) {
      document.querySelector(".signin-btn").removeAttribute("disabled");
    });
  },
  created() {
    this.userCheck();
  }
};
</script>

<style scoped>
#app {
  position: relative;
}

.top-bar .nav .nav-link {
  padding: 0rem 0.5rem;
  color: white;
}
.top-bar .nav .nav-link:hover {
  color: #d9d9d9;
}
</style>



