<template>
  <div class="container mb-5">
    <loading :active.sync="isLoading"></loading>
    <div class="page-title">
      <h1 class="display-5 mb-2">搜尋關鍵字：{{keyWord}}</h1>
      <p class="lead">
        <Breadcrumb :clickCategory="'all'" :page="'search'" @productSort="sortChange" />
      </p>
      <hr class="mb-0" />
    </div>
    <div class="row mb-4">
      <div class="col-12 d-flex justify-content-between">
        <div>
          <p>符合的結果：{{searchProducts.length}} 筆</p>
        </div>
        <div>
          <select class="custom-select custom-select-sm" v-model="sort">
            <option value selected>商品排序</option>
            <option value="desc">按價格排序：由高到低</option>
            <option value="asc">按價格排序：由低到高</option>
          </select>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-lg-3 col-md-4 col-sm-6 col-12 mb-4" v-for="item in searchProducts" :key="item.id">
        <ProductCard :product="item" />
      </div>
    </div>
  </div>
</template>

<script>
import ProductCard from "../ProductCard";
import Breadcrumb from "../Breadcrumb";

export default {
  components: {
    ProductCard,
    Breadcrumb
  },
  data() {
    return {
      allProducts: [],
      keyWord: "",
      sort: "",
      isLoading: false
    };
  },
  methods: {
    getSearchProducts() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products`;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        vm.allProducts = response.data.products.filter(item => {
          return item.is_enabled == 1;
        });
        vm.isLoading = false;
      });
    },
    sortChange(value) {
      const vm = this;
      vm.sort = value;
    }
  },
  computed: {
    searchProducts() {
      const vm = this;
      const temp_array = vm.allProducts.filter(item => {
        return (
          item.title.indexOf(vm.keyWord) !== -1 ||
          item.category.indexOf(vm.keyWord) !== -1
        );
      });
      if (vm.sort != "") {
        return temp_array.sort((a, b) => {
          return vm.sort === "asc"
            ? a["origin_price"] > b["origin_price"]
            : a["origin_price"] < b["origin_price"];
        });
      } else {
        return temp_array;
      }
    }
  },
  watch: {
    $route(to, from) {
      this.keyWord = to.params.keyword;
    }
  },
  created() {
    this.keyWord = this.$route.params.keyword;
    if (this.allProducts.length == 0) {
      this.getSearchProducts();
    }
  }
};
</script>

<style scoped>
.page-title {
  padding: 2.5rem 0;
}
</style>



